.main-header__sidebar, .main-header__container, .main-header__fullinfo {
  background: #2e2e2e;
}

.main-header__menu {
  color: #fff;
  background: #EF902D;
}

.main-header__menu:active {
  color: #fff;
  background: #ed8315;
}

.no-touch .main-header__menu:active {
  color: #fff;
  background: #ed8315;
}

.header {
  border-bottom: 7px solid #EF902D;
  background: #2e2e2e;
}

.header .header__logos {
  bottom: -7px;
  background: rgba(255, 255, 255, 0.3);
}

.header .header__logos img {
  max-height: 100%;
}

.toolbar__col:not(:last-child) {
  border-right-color: rgba(255, 255, 255, 0.4) !important;
}

.toolbar__button {
  color: #fff;
  background: #EF902D;
}

.toolbar__button:active {
  color: #fff;
  background: #ed8315;
}

.no-touch .toolbar__button:active {
  color: #fff;
  background: #ed8315;
}

.btn.fieldset_last,
.modal-terms__submit {
  color: #fff;
  background: #EF902D;
}

.btn.fieldset_last:active,
.modal-terms__submit:active {
  color: #fff;
  background: #ed8315;
}

.no-touch .btn.fieldset_last:active, .no-touch
.modal-terms__submit:active {
  color: #fff;
  background: #ed8315;
}

.menu__header {
  color: #fff;
  background: #EF902D;
}

.menu__header:active {
  color: #fff;
  background: #ed8315;
}

.no-touch .menu__header:active {
  color: #fff;
  background: #ed8315;
}

[data-scrollbar="rounded"]::-webkit-scrollbar {
  display: initial !important;
}

[data-scrollbar="rounded"]::-webkit-scrollbar-thumb {
  background: #EF902D;
}

.no-touch [data-scrollbar="rounded"]::-webkit-scrollbar-thumb:hover {
  background: #ed8315;
}

[data-scrollbar="rounded"]::-webkit-scrollbar:vertical {
  width: 12px;
  background: linear-gradient(to right, transparent 5px, #EF902D 5px, #EF902D 6px, transparent 6px);
}

[data-scrollbar="rounded"]::-webkit-scrollbar:horizontal {
  height: 12px;
  background: linear-gradient(to bottom, transparent 5px, #EF902D 5px, #EF902D 6px, transparent 6px);
}

[data-scrollbar="rounded"]::-webkit-scrollbar-thumb {
  border-radius: 6px;
}
