@import "helpers/_colors";
@import "helpers/_helpers";
@import "helpers/_mixins";


.main-header {
	&__sidebar,
	&__container,
	&__fullinfo {
		background: #2e2e2e; }
	&__menu {
		color: $white;
		background: #EF902D;
		@include active {
			color: $white;
			background: #ed8315; } } }

.header {
	border-bottom: 7px solid #EF902D;
	background: #2e2e2e;
	.header__logos {
		bottom: -7px;
		background: rgba($white, 0.3);
		img {
			max-height: 100%; } } }

.toolbar {
	&__col:not(:last-child) {
		border-right-color: rgba($white, 0.4) !important; }
	&__button {
		color: $white;
		background: #EF902D;
		@include active {
			color: $white;
			background: #ed8315; } } }

.btn.fieldset_last,
.modal-terms__submit {
	color: $white;
	background: #EF902D;
	@include active {
		color: $white;
		background: #ed8315; } }

.menu {
	&__header {
		color: $white;
		background: #EF902D;
		@include active {
			color: $white;
			background: #ed8315; } } }

//scrollbar
[data-scrollbar="rounded"] {
	&::-webkit-scrollbar {
		display: initial !important; }
	&::-webkit-scrollbar-thumb {
		background: #EF902D;
		@include hover {
			background: #ed8315; } } }

[data-scrollbar="rounded"] {
	&::-webkit-scrollbar {
		&:vertical {
			width: 12px;
			background: linear-gradient(to right, transparent 5px, #EF902D 5px, #EF902D 6px, transparent 6px); }
		&:horizontal {
			height: 12px;
			background: linear-gradient(to bottom, transparent 5px, #EF902D 5px, #EF902D 6px, transparent 6px); } }
	&::-webkit-scrollbar-thumb {
		border-radius: 6px; } }
